
import { Image } from 'antd';

interface UrlImage {
    link?: string;
}

const Images = (src: UrlImage) => {

    return (
        <Image       
            style={{ borderRadius: 50 }}
            width={90}
            height={90}
            src={src.link}
            fallback="data:image/png;base64,"
        />
    );
}

export default Images;